
/**
 * @abstract
 */
export abstract class StorageAdapter {
/**
 * @abstract
 * @param {?} key
 * @return {?}
 */
getItem(key: string) {}
/**
 * @abstract
 * @param {?} key
 * @param {?} value
 * @return {?}
 */
setItem(key: string, value: string) {}
/**
 * @abstract
 * @param {?} key
 * @return {?}
 */
removeItem(key: string) {}
/**
 * @abstract
 * @return {?}
 */
clear() {}
/**
 * @abstract
 * @return {?}
 */
getSupported() {}
}